        padding-inline-start: 0;

        .project-listing {
            display: block;
            list-style: none;
            margin: 1.5em 0em;

            .project-listing-header {
                margin-bottom: 1em;

                a {
                    @include icon-forward;
                    text-decoration: none;

                    h3 {
                        display: inline-block;
                        margin: 0; }

                    i {
                        margin-left: 10px; } }

