                        margin: 0;

                        li {
                            font-size: 0.75em;
                            list-style: none;
                            display: inline-block;

                            &:first-child {
                                margin-left: 0; }

                            &:after {
                                margin: 0.5em;
                                content: "â€¢"; }

                            &:last-child:after {
                                content: none; } } } } }

            p {} } }

    a {
        @include icon-forward;
